import React, { useState } from 'react'

export const ScrollDetector = () => {
  const [navbar, setNavbar] = useState(false)

  const fixNavbar = () => {
    if (window.scrollY >= 1) {
      setNavbar(true)
    } else {
      setNavbar(false)
    }
  }

  if (typeof window !== `undefined`) {
    window.addEventListener('scroll', fixNavbar)
  }

  return navbar
}
